import React, { FC, useEffect } from "react";
import { SubmitHandler, useForm } from "react-hook-form";
import * as yup from "yup";
import * as styles from './styles.module.scss';
import { useDispatch } from 'react-redux';
import { productActions, productSelectors } from 'src/store/products';

//  
type Inputs = {
  name: string;
  description: string;
  price: number;
  img: string;
};
export interface IProductForm {
  id?: string;
  price?: number;
  index?: number;
  image?: string;
  name?: string;
  description?: string;
  addMode?: boolean;
  addAdminMode?: boolean;
  onClose?: () => void;
}

//  
const schema = yup
  .object()
  .shape({
    name: yup.string().required(),
    description: yup.string().required(),
    price: yup.number().required(),
    img: yup.string(),
  })
  .required();

export const ProductForm: FC<IProductForm> = ({ id, price, image, description, name, addMode, index, onClose, addAdminMode }) => {
  const {
    register,
    handleSubmit,
    reset,
    formState: { errors, isValid },
  } = useForm<Inputs>({
    mode: "onBlur",
    defaultValues: {
      name: name || '',
      description: description || '',
      price: price || 0,
      img: image || '',
    },
  });
  const dispatch = useDispatch();

  const editProduct = (id: string, name: string, image: string, price: number, description: string) => { dispatch(productActions.edit({ id, name, image, price, description })) }
  const addProduct = (name: string, image: string, price: number, description: string) => dispatch(productActions.addNew({ name, image, price, description }));


  const customHandleSubmit: SubmitHandler<Inputs> = (values) => {
    debugger
    if (addAdminMode) {
      addProduct(values.name, values.img, values.price, values.description)
    } else {
      editProduct(id, values.name, values.img, values.price, values.description)
    }
    reset();
    onClose()
  };

  return (<form className={styles.form} onSubmit={handleSubmit(customHandleSubmit)}>
    <h1>/ </h1>
    <div className={styles.formInput}>
      <label htmlFor="name"></label>
      <input
        id="name"
        type="text"
        {...register("name", {
          required: true,
          minLength: {
            value: 3,
            message: " 3 !",
          },
        })}
      />
      {errors.name && <p style={{ color: "red" }}>{errors.name.message}</p>}
    </div>
    <div className={styles.formInput}>
      <label htmlFor="description"></label>
      <input id="description"
        type="text"
        {...register("description", {
          required: true,
          minLength: {
            value: 3,
            message: " 3 !",
          },
        })} />
      {errors.description && <p style={{ color: "red" }}>{errors.description.message}</p>}
    </div>
    <div className={styles.formInput}>
      <label htmlFor="price"></label>
      <input id="price"
        type="number"
        {...register("price", {
          required: true,
        })} />
      {errors.price && <p style={{ color: "red" }}>{errors.price.message}</p>}
    </div>
    <div className={styles.formInput}>
      <label htmlFor="img">  </label>
      <input id="img"
        type="text"
        {...register("img")} />
      {errors.img && <p style={{ color: "red" }}>{errors.img.message}</p>}
    </div>
    <hr />
    <button type="submit" disabled={!isValid}></button>
  </form>);
};

